import NcListItem from '@nextcloud/vue/dist/Components/NcListItem.js';
import AppManagement from '../../mixins/AppManagement.js';
export default {
  name: 'DaemonEnableSelection',
  components: {
    NcListItem
  },
  mixins: [AppManagement],
  props: {
    daemon: {
      type: Object,
      required: true,
      default: () => {}
    },
    isDefault: {
      type: Boolean,
      required: true,
      default: () => false
    },
    daemons: {
      type: Array,
      required: true,
      default: () => []
    },
    appId: {
      type: String,
      required: true
    }
  },
  computed: {
    itemTitle() {
      return this.daemon.name + ' - ' + this.daemon.display_name;
    }
  },
  methods: {
    closeModal() {
      this.$emit('close');
    }
  }
};